
#intro {
  height: 150vh;
  background: white;
  cursor: pointer;
}
#intro section {
    height: 50vh;
    width: 100%;
    position: fixed;
}
#intro section h1 {
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform: translate3d(-50%, -50%, 0);
              transform: translate3d(-50%, -50%, 0);
      margin: 0;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      font-size: 1.999rem;
}
#intro .mask {
    position: fixed;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 100%;
    height: 50vh;
    z-index: 45;
    background: transparent;
    pointer-events: none;
}
#intro .mask section {
      position: absolute;
      top: 0;
      pointer-events: auto;
}
#intro #bw-mask {
    top: 50%;
}
#intro #title {
    height: 100vh;
    background: white;
    color: black;
    z-index: 40;
}
#intro #bw {
    top: 50vh;
    background: black;
    color: white;
    z-index: 50;
}
#intro #colour {
    top: 100vh;
    background: blue;
    color: white;
    z-index: 45;
}



